body {
  background-repeat: no-repeat;

  @include x1 {
    background-image: 
      url('../images/plate_mobile@1x.png'),
      url('../images/bg/bg1-m@1x-min.png'),
      url('../images/bg/bg2-m@1x-min.png');
  }
  
  @include x2 {
    background-image: 
      url('../images/plate_mobile@2x.png'),
      url('../images/bg/bg1-m@2x-min.png'),
      url('../images/bg/bg2-m@2x-min.png');
  }

  background-size: 
    113px 99px,
    100vw 474px,
    100vw 1337px;
  
  background-position:
    right bottom,
    center top,
    center bottom 181px;
  
  @include tablet {
    @include x1 {
      background-image: 
        url('../images/plate_tablet@1x.png'),
        url('../images/bg/bg1-t@1x-min.png'),
        url('../images/bg/bg2-t@1x-min.png');
    }
    
    @include x2 {
      background-image: 
        url('../images/plate_tablet@2x.png'),
        url('../images/bg/bg1-t@2x-min.png'),
        url('../images/bg/bg2-t@2x-min.png');
    }

    background-size: 
      184px 90px,
      100% 622px,
      100% 1519px;
    
    background-position:
      right bottom,
      center top,
      center bottom;
  }

  @include desktop-bg {
    overflow-x: hidden;

    @include x1 {
      background-image: 
        url('../images/plate_desktop@1x.png')
        url('../images/bg/bg1-d@1x-min.png'),
        url('../images/bg/bg2-d@1x-min.png');
    }
    
    @include x2 {
      background-image: 
        url('../images/plate_desktop@2x.png')
        url('../images/bg/bg1-d@2x-min.png'),
        url('../images/bg/bg2-d@2x-min.png');
    }

    background-size: 
      302px 175px,
      100% 1036px,
      100% 2073px;
    
    background-position:
      right bottom,
      center top,
      center bottom -212px;
  }
}

