.section-registration .registration-thumb {
  position: relative;
  z-index: 1;
}

.section-registration .container::after {
  content: '';
  display: none;
  position: absolute;

  z-index: 0;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;

  @include tablet {
    display: block;
    transform: translate(-35px, -345px);
    height: 492px;
    width: 768px;

    @include x1 {
      background-image: url('../images/reg-bak-t@1x.png');
    }

    @include x2 {
      background-image: url('../images/reg-bak-t@2x.png');
    }
  }

  @include desktop-bg {
    height: 621px;
    width: 1203px;
    transform: translate(-35px, -335px);

    @include x1 {
      background-image: url('../images/reg-bak-d@1x.png');
    }

    @include x2 {
      background-image: url('../images/reg-bak-d@2x.png');
    }
  }
}